
@font-face {
    font-family: "Kollektif";
    src: url(fonts/Kollektif.ttf) format("truetype");
}

@font-face {
    font-family: "Orkney";
    src: url(fonts/Orkney-Regular.otf) format("opentype");
}


.literallyjustpadding {
    margin-top: 18vh;
}

.main-content {
    border-radius: 25px;
    border: 2px solid #BABABA;
    -webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
            box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
 }

li {
    list-style: none;
}

h1 {
    padding-top: 5%;
    font-size: 70px;
    margin-bottom: 0px;
}

body {
    margin:0;
    height: 100%;
    width: 100%;
    font-family: Orkney;
    overflow-x: hidden;
}

.container-fluid {
    padding: 0;
}

.icon-pic {
    margin: 1% 0%;
    border-radius: 5px;
}

.pic {
    border-radius: 25px;
    max-width: 23vh;
    max-height: 23vh;
    margin: 2% 2%;
    float:left;
}

a {
    color: #7A7A7A;
}

.parallax-2 {
    min-height: 10vh;
}


.headerbox h1 {
    font-size: 80px;
    letter-spacing: 4px;
 }

.parallax-header {
    margin-bottom: 0;
 }

.content-section {
    padding-top: 2%;
}

.list-section {
    margin-top: 5%;
 }

.force-line {
    display: inline-block;
}

.contain-text {
    display: inline-block;
 }

.icons-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

p, ul{
    font-size: 1.3em;
}

h2 {
    font-size: 20px;
}

button {
  text-align: center;
  margin: auto;
  background: #FFF;
  border-radius: 25px;
  -webkit-transition-duration: 0.4s; /* Safari */
  -o-transition-duration: 0.4s;
     transition-duration: 0.4s;
}

button:hover {
    background-color: #d6d7d8;
}


